.circles {
  display: flex;
  justify-content: space-around;
  margin-bottom: 15px;
}
.circles .item {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.circles .item h1 {
  color: white;
  font-weight: 500;
  margin-top: 7.5px;
}
.circles .ant-progress-circle-trail {
  stroke: #f5f5f5;
  opacity: 0.2;
}
.circles .ant-progress-text {
  color: white;
}
.circles .ant-radio-group {
  display: flex !important;
  justify-content: space-around;
  width: 100%;
}
.circles .ant-radio-wrapper:hover {
  transition: 0.2s;
  transform: scale(1.05);
}
.circles .ant-radio-wrapper-checked {
  transition: 0.2s;
  transform: scale(1.12) !important;
}
.circles .ant-radio-inner {
  display: none;
}
