.home-page {
  background: url(/static/media/home-background.94a4272d.png);
  background-size: cover;
  color: white;
  height: 100vh;
  text-align: center;
  width: 100vw;
}
.home-page h1 {
  color: white;
}
.home-page .sidenav {
  left: 2.5vw;
  top: 25vh;
  font-size: 1.25rem;
  position: absolute;
  display: flex;
  justify-content: center;
  -webkit-writing-mode: vertical-rl;
      -ms-writing-mode: tb-rl;
          writing-mode: vertical-rl;
  -webkit-text-orientation: upright;
          text-orientation: upright;
}
.home-page .sidenav h1 {
  font-family: Garamond;
  letter-spacing: 0.2rem;
  margin-bottom: 5vh;
}
.home-page .sidenav h1:hover {
  cursor: pointer;
}
.home-page .slidestats {
  line-height: 20px;
  position: absolute;
  text-align: left;
  left: 5vh;
  top: 2vh;
}
.home-page .slidestats h1 {
  font-family: "Helvetica Neue";
  font-weight: 300;
  font-size: 1rem;
  letter-spacing: 0.2rem;
}
.home-page .sidecircle {
  position: absolute;
  height: 500px;
  width: 500px;
  border-radius: 50%;
  top: 17.5%;
  left: 87.5vw;
  background-color: white;
}
.home-page .sidecircle:hover {
  opacity: 0.5;
  transition: all 0.25s ease;
  cursor: pointer;
  background-image: url(/static/media/planet.93ad548e.png);
}
.home-page .begin-button {
  bottom: calc(-25vw);
  height: 40vw;
  margin-left: 30vw;
  margin-right: 30vw;
  position: absolute;
}
.home-page .begin-button h1 {
  font-family: "Helvetica Neue";
  font-size: 2rem;
  margin-top: 20vh;
  position: absolute;
  text-align: center;
  width: 100%;
  z-index: 10;
}
.home-page .begin-button h1:hover {
  cursor: pointer;
}
.home-page .begin-button img {
  -webkit-animation: rotation 120s infinite linear;
          animation: rotation 120s infinite linear;
  height: 100%;
}
.home-page .center {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  width: 100vw;
}
.home-page .center .title {
  background-image: url(/static/media/dot-grid.320d6d39.svg);
  padding: 20px 20px 60px 20px;
}
.home-page .center .title h1 {
  font-size: 13.5rem;
  letter-spacing: 0.2rem;
  margin-bottom: 0px;
  line-height: 230px;
}
.home-page .center .title p {
  font-family: Garamond;
  margin-top: 20px;
  font-size: 3rem;
}
@-webkit-keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
@keyframes rotation {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(359deg);
  }
}
